import {
  Image,
  Box,
  Heading,
  Flex,
  Text,
  Button,
  HStack,
  Wrap,
  WrapItem,
  Avatar,
} from "@chakra-ui/react";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { BsFillAlarmFill } from "react-icons/bs";
import { ImSpoonKnife } from "react-icons/im";
import { MdOutlineSlowMotionVideo, MdKitchen } from "react-icons/md";
import { useState, useEffect } from "react";
import axios from "axios";
import moment from "moment";
const ImageSlider = () => {
  const [recipe, setRecipe] = useState([]);
  const api = "http://95.111.202.157:8001/api/popularRecipes";

  useEffect(() => {
    const loadPost = async () => {
      const response = await axios.get(api);
      console.log("ajay kushwah", response);

      setRecipe(response.data);
    };

    loadPost();
  }, []);

  return (
    <Carousel infiniteLoop>
      {recipe.map((slide) => {
        return (
          <Box>
            <Flex
              maxW={1024}
              mx={"auto"}
              mb={10}
              mt={10}
              bg="#EBF8FF"
              borderRadius="15px"
            >
              <Box w={700}>
                <Button borderRadius="10px" mt={10} ml={-300}>
                  <MdKitchen /> Hot Receipe
                </Button>
                <Heading
                  fontSize="4xl"
                  mt={25}
                  ml={5}
                  mr={20}
                  textAlign="justify"
                >
                  {slide.recipeId.title}
                </Heading>
                <Text
                  fontSize={"sm"}
                  ml={10}
                  mt={5}
                  mb={5}
                  textAlign="justify"
                  mr={20}
                >
                 {slide.recipeId.description}
                </Text>
                <HStack ml={10} justifyItems={"space-between"}>
                  <Button borderRadius="10px" bgColor="#EDFDFD">
                    <BsFillAlarmFill />
                    {slide.recipeId.cookTime}
                  </Button>
                  <Button borderRadius="10px" bgColor="#EDFDFD">
                    <ImSpoonKnife />
                    {slide.recipeId.categoryId.categoryName}
                  </Button>
                </HStack>
                <Wrap
                  spacing="160px"
                  mt={20}
                  ml={10}
                  mb={5}
                  justifyItems={"space-between"}
                >
                  <WrapItem>
                    <Avatar
                      size="sm"
                      name="Kent Dodds"
                      src="https://bit.ly/kent-c-dodds"
                    />
                    <Box pl={3}>
                      <Heading fontSize={"sm"}>{slide.recipeId.userId.firstName} {slide.recipeId.userId.lastName}</Heading>
                      <Text fontSize={"sm"}>{moment(slide.recipeId.userId.createdAt).format("MMM Do YY")}</Text>
                    </Box>
                  </WrapItem>
                  <Button borderRadius="10px" bgColor="#000" colorScheme="#fff">
                    View Receipe
                    <MdOutlineSlowMotionVideo />
                  </Button>
                </Wrap>
              </Box>
              <Box w={700}>
                <Image src={"http://95.111.202.157:8001/" + slide.recipeId.image} height="500px" />
              </Box>
            </Flex>
          </Box>
        );
      })}
    </Carousel>
  );
};

export default ImageSlider;
